import axios from 'axios'

class Http{
    constructor(){
    this.instance = axios.create({
        baseURL: 'https://localhost:8080/get',
        timeout: 60000,
        //   headers: {'X-Custom-Header': 'foobar'}
        });
    }
    get({url,params = {}}){
        return new Promise((resolve,reject) =>{
            this.instance.get(url,{
                params
            })
            .then((result) =>{
                resolve(result)
            })
            .catch((err)=>{
                reject(err)
            })
        })
    }
}

export default Http