export default {
    path:'/movie',
    component:() => import('@/views/Movie'),
    children:[
        {
            path:'city',
            component:() => import('@/components/City')
        },
        {
            path:'nowPlaying',
            component:() => import('@/components/NowPlaying')
        },
        {
            path:'comingSoon',
            component:() => import('@/components/ComingSoon')
        },
        {
            path:'search',
            component:() => import('@/components/Search')
        },
        {
            path:'detail/:Id',
            name:'detail',
            component:() => import('@/components/Details/detail')
        },
        {
            path:'detail2/:Id',
            name:'detail2',
            component:() => import('@/components/Details/detail2')
        },

        /* {
            path:'/Movie/detail/:Id',
            components:{
                detail : () => import('@/components/NowPlaying'),
                default:() => import('@/views/Movie/detail')
            },
            props :{
                detail : true
            }
        },
        {
            path:'/Movie/detail2/:Id',
            components:{
                detail : () => import('@/components/ComingSoon'),
                default:() => import('@/views/Movie/detail2')
            },
            props :{
                detail : true
            }
        }, */
        
        {
            path:'/movie',
            redirect:'/movie/NowPlaying'
        }
    ]
}